.component-help {
  width: 70%;
  margin: 0 auto;
  padding: 2.1vw 0;
  height: 100%;
}
.component-help .component-help-leftbar {
  width: 15%;
  position: fixed;
}
.component-help .card {
  margin-left: 12%;
  width: 88%;
  background-color: #ffffff;
  box-shadow: 0vw 0vw 0.5vw 0vw rgba(215, 215, 215, 0.5);
  border-radius: 0vw 0vw 0.2vw 0.2vw;
  padding: 2.6vw;
}
.component-help .card .ant-breadcrumb .ant-breadcrumb-link,
.component-help .card .ant-breadcrumb .ant-breadcrumb-separator {
  color: #999;
  font-size: 0.7vw;
  line-height: 1.9vw;
}
.component-help .card .ant-breadcrumb .ant-breadcrumb-separator {
  margin: 0 4px;
}
